// console.log = function() {}

function success(position) {
    console.log('Success');
    console.log(position.coords.lat);
    console.log(position.coords.lng);
}    
// Error callback
function error(position) {
    console.log('Could not find you!');
}

navigator.geolocation.getCurrentPosition = function(success, error){
    var customPosition = {};
    customPosition.coords = {};
    customPosition.coords.lat = 43.6534817;
    customPosition.coords.lng = -79.3839347;
    success(customPosition); // Ottawa
}

